$breakpoint-actual-mobile: 375px;

$breakpoint-mobile: 480px;

$breakpoint-tablet: 768px;

$breakpoint-desktop: 1024px;

$breakpoint-large-desktop: 1440px;

$main-accent-flat: #00a967;
$pure-black: #000000;
$light-grey: #dfdfdf;
$pure-white: #ffffff;
@mixin main-gradient {
	background: rgb(0, 209, 127);
	background: linear-gradient(
		156deg,
		rgba(0, 209, 127, 1) 0%,
		rgba(30, 126, 77, 1) 100%
	);
}

@mixin flex-center {
	display: flex;
	align-content: center;
	align-items: center;
	justify-content: center;
}

@mixin bp-small {
	@media screen and (min-width: $breakpoint-mobile) {
		@content;
	}
}

@mixin bp-med {
	@media screen and (min-width: $breakpoint-tablet) {
		@content;
	}
}

@mixin bp-large {
	@media screen and (min-width: $breakpoint-desktop) {
		@content;
	}
}

@mixin bp-ld {
	@media screen and (min-width: $breakpoint-large-desktop) {
		@content;
	}
}

@mixin iPhone-x {
	@media only screen and (device-width: $breakpoint-actual-mobile) {
		@content;
	}
}

